.projects {
  min-height: 100vh;
  background-color: #1f1f20;
  display: flex;
  justify-content: center;
  padding: 100px 0;
}

.container {
  width: 80%;

  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.projectsItems {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}


@media (max-width: 1030px) {
  .title{
    margin: 0;
    margin-bottom: 50px;
  }
}